body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card > .img-container> img {
	border-radius: 2px;
	height: 200px;
	margin: 1rem;
	position: relative;
	width: 200px;
	box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  }

	.card> .img-container {
		overflow: hidden;
		text-align: center;
		background:black;
	}
	.card > .img-container,
	img {
		height: 100%;
		width: 100%;
	}
  .card> .img-container:hover {
		background: green;
	}
.CardContainer {
    margin-bottom: 50px;
    padding: 0;
}

.gameMessage {
    text-align: center;
    max-width: 100%;
    -webkit-align-content: center;
            align-content: center
}

.gameMessage p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color: #e46471b4;
    color: black;
    padding: 20px 0;
    margin-bottom: 0px;
}

.gameScores {
    text-align: center;
    max-width: 100%;
    -webkit-align-content: center;
            align-content: center;
    margin-bottom: 10px;
}
.gameScores p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color:#e46471b4;
    color: black;
    padding: 20px 0;
    margin-bottom: 25px;
}
